<template>
  <div id="app">
    <TopHeader></TopHeader>
    <MainBox :dataechatrs="dataEcharts"></MainBox>
    <router-view />
  </div>
</template>
<script>
import { getEcharts } from "@/request/api.js";
export default {
  name: "App",
  components: {
    TopHeader: () => import("@/components/TopHeader.vue"),
    MainBox: () => import("@/components/MainBox.vue"),
  },
  data() {
    return {
      dataEchartsStream: [],
      dataEcharts: [],
    };
  },
  created() {
    this.getEchartsData();
    this.getEchartsDataStream();
    this.setScale();
  },
  mounted() {
    window.addEventListener("resize", this.setScale());
  },
  methods: {
    // 获取data
    async getEchartsData() {
      let dataEcharts = await getEcharts();
      this.dataEcharts = dataEcharts.data;
      console.log("dataEcharts: ", this.dataEcharts);
    },
    // SSE实现实时数据获取,不支持ie浏览器,支持自动断线重连
    getEchartsDataStream() {
      const source = new EventSource("http://localhost:3000/getData");
      // source.readystate  客户端 与 服务器 正在链接
      // source.readystate 1 客户端 与 服务器 已经 建立链接
      // source.readystate 2 客户端 与 服务器 已经 断开链接

      console.log(source);
      console.log("source.readyState: ", source.readyState);
      console.log("EventSorce.connecting: ", EventSource.CONNECTING);
      console.log("EventSorce.open: ", EventSource.OPEN);
      console.log("EventSorce.CLOSED: ", EventSource.CLOSED);

      source.onopen = function (event) {
        console.log("1111111", event);
      };
      source.onmessage = function (event) {
        console.log("2222222222", event.data);
        // console.log("5555555", this.dataEchartsStream);

        JSON.parse(event.data).forEach((item) => {
          this.dataEchartsStream = item.data;
        });
        console.log("5555555", this.dataEchartsStream);
      };
      source.onerror = function (event) {
        console.log("333333333", event);
      };
    },
    setScale() {
      //设置设计图宽度,高度
      const designWidth = 1920;
      const designHeight = 1080;
      const nowWidth = document.documentElement.clientWidth;
      const nowHeight = document.documentElement.clientHeight;
      const app = document.getElementById("app");
      const designRatio = designWidth / designHeight;
      const nowRatio = nowWidth / nowHeight;
      const scale =
        nowRatio < designRatio
          ? nowWidth / designWidth
          : nowHeight / designHeight;
      app.style.transform = `scale(${scale}) translate(-50%, -50%)`;
    },
  },
};
</script>
