  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  overflow: hidden;
  width: 1920px;
  height: 1080px;
  background-image: url("@/assets/global.png");
  h1 {
    margin: 0;
  }
  .com-bgc();
  // 自适应将transform变化起点设置为左上角
  transform-origin: 0 0;
  position: fixed;
  top: 50%;
  left: 50%;
}

